import { searchHot , searchWord , searchSuggest } from '../../common/api.js'
import '../../common/iconfont.css'
export default {
	data() {
		return {
			searchHot : [],
			searchWord : '',
			historyList : [],
			searchType : 1,
			searchList : [],
			suggestList : []
		}
	},
	onLoad(){
		searchHot().then((res)=>{
			if(res[1].data.code == '200'){
				this.searchHot = res[1].data.data;
			}
		});
		uni.getStorage({
		    key: 'searchHistory',
		    success:(res)=>{
		        this.historyList = res.data;
		    }
		});
	},
	methods: {
		handleToSearch(){
			this.historyList.unshift(this.searchWord);
			this.historyList = [...new Set(this.historyList)];
			if(this.historyList.length > 10){
				this.historyList.length = 10;
			}
			uni.setStorage({
			    key: 'searchHistory',
			    data: this.historyList
			});
			this.getSearchList(this.searchWord);
		},
		handleToClear(){
			uni.removeStorage({
				key:'searchHistory',
				success:()=>{
					this.historyList = [];
				}
			});
		},
		getSearchList(word){
			searchWord(word).then((res)=>{
				if(res[1].data.code == '200'){
					this.searchList = res[1].data.result.songs;
					this.searchType = 2;
				}
			});
		},
		handleToClose(){
			this.searchWord = '';
			this.searchType = 1;
		},
		handleToSuggest(ev){
			let value = ev.detail.value;
			if(!value){
				this.searchType = 1;
				return;
			}
			searchSuggest(value).then((res)=>{
				if(res[1].data.code == '200'){
					this.suggestList = res[1].data.result.allMatch;
					this.searchType = 3;
				}
			});
		},
		handleToWord(word){
			this.searchWord = word;
			this.handleToSearch();
		},
		handleToDetail(songId){
			uni.navigateTo({
				url: '/pages/detail/detail?songId='+songId
			});
		}
	}
}
